import {Component} from '@angular/core';
import {NavController, NavParams, ToastController} from 'ionic-angular';
import {CartPage} from '../cart/cart';
import {CartService} from '../../providers/cart-service-mock';
import {Storage} from '@ionic/storage';

@Component({
    selector: 'page-item-detail',
    templateUrl: 'item-detail.html'
})
export class ItemDetailPage {

  item: any;
  options: any;
  qtd: number = 1;
  orderOptions = [];
  orderPrices = [];
  orders: Array<any> = [];
  extrasum:number;
  idNumber: number = Math.floor(Math.random() * 1000);

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams, 
    public toastCtrl: ToastController, 
    public storage: Storage,
    public cartService: CartService) {
      this.item = this.navParams.data;
      let item = this.item;
      console.log('itemId: '+item.itemId);
      if (this.item.options){
        this.options = this.item.options;
      }
      this.checkForUpdate(item);
  }

  customizeToggle(){
    let optionsPanel = document.getElementById("options");
    if (optionsPanel.style.display === "none") {
      optionsPanel.style.display = "block";
    } else {
      optionsPanel.style.display = "none";
    }
  }

  checkForUpdate(item){
    if (item.itemId){
      this.storage.get('orders').then((data) => {
        this.orders = data;
        console.log(this.orders);
        this.orders.forEach((val, i) => {
          if (val.itemId == this.item.itemId){
            console.log(this.item);
          }else{
            console.log('Nope');
          }
        });
      });
    }
  }
  addToOrder(item, name, price){
    if(this.orderOptions.indexOf(name) === -1) {
      this.orderOptions.push(name);
      item.selectedOptions = this.orderOptions;
      for (var i in this.options) {
        if (this.options[i].name == name) {
            this.options[i].selected = true;
           break;
        }
      }
      this.orderPrices.push(price);
    }else{
      this.orderOptions.splice(this.orderOptions.indexOf(name),1);
      item.selectedOptions = this.orderOptions;
      this.orderPrices.splice(this.orderPrices.indexOf(price),1);
      for (i in this.options) {
        if (this.options[i].name == name) {
            this.options[i].selected = false;
           break;
        }
      }
    }

    if (this.orderOptions.length > 0){
      item.extrasum = this.orderPrices.reduce(this.getSum);
    }else{
      item.extrasum = 0;
    }
    console.log(item);

    return;
  }

  getSum(total, num) {
    return total + num;
  }

  chooseDrink(item, name, price){
    this.orderOptions = [];
    this.orderOptions.push(name);
    item.selectedOptions = this.orderOptions;
    this.orderPrices.push(price);
    item.extrasum = 0;
  }

  // minus adult when click minus button
  minusQtd() {
    this.qtd--;
  }
  // plus adult when click plus button
  plusQtd() {
    this.qtd++;
  }

  updatecart(item, qtd) {
    this.cartService.updateCart(item, qtd);
    this.navCtrl.pop();
  }

  addcart(item, qtd) {
    let name = item.name;
    item.itemId = this.idNumber;
    this.cartService.addtoCart(item, qtd)
      .then(item => {
        let toast = this.toastCtrl.create({
            message: name + ' added to Cart',
            cssClass: 'mytoast',
            duration: 2000
        });
      toast.present(toast);
    });
    this.navCtrl.pop();
  }

  openCart() {
    this.navCtrl.push(CartPage);
  }

}



